<style scoped>
    .header {
        margin-bottom: 10px;
        overflow: hidden;
    }
    .page {
        margin-top: 10px;
    }
    .fl {
        float: left;
    }
    .fr {
        float: right;
    }
    .mt {
       margin-top: 10px; 
    }
</style>
<template>
    <div>
        <div class="header">
            <RadioGroup class="type fl" @on-change="changeType" v-model="type" type="button">
                <Radio label="0"></Radio>
                <Radio label="1"></Radio>
                <Radio label="2"></Radio>
                <Radio label="3"></Radio>
            </RadioGroup>
            <Button class="post fr" @click="ShowWorkOrderModal" type="primary"></Button>
        </div>
        
        <Table :columns="columns" :data="data"></Table>
        <Page class="page" @on-change="toPage" :total="allData.length" />
        <Modal
            v-model="workOrderModal"
            title=""
            :loading="workOrderModalLoading"
            @on-ok="ok"
            @on-cancel="cancel">
            <Row :gutter="5">
                <Col span="4">
                    <div style="text-align: center;line-height: 32px;">
                        
                    </div>
                </Col>
                <Col span="10">
                    <Select v-model="currentType" @on-change="getSubType" style="width: 100%;">
                        <Option v-for="item in typeList" :value="item.id" :key="item.id">{{ item.type_name }}</Option>
                    </Select>
                </Col>
                <Col span="10">
                    <Select v-model="currentType2" @on-change="changeSelectTypeId" v-if="typeSubList.length" style="width: 100%;">
                        <Option v-for="item in typeSubList" :value="item.id" :key="item.id">{{ item.type_name }}</Option>
                    </Select>
                </Col>
            </Row>
            <Row class="mt" :gutter="5">
                <Col span="4">
                    <div style="text-align: center;line-height: 32px;">
                        
                    </div>
                </Col>
                <Col span="20">
                    <Select v-model="business" style="width: 100%;">
                        <Option v-for="item in businessList" :value="item.business_number" :key="item.business_number">{{ item.machine_number }}</Option>
                    </Select>
                </Col>
            </Row>
            <Row class="mt" :gutter="5">
                <Col span="24">
                    <Input v-model="workOrderContent" type="textarea" placeholder="" />
                </Col>
            </Row>
        </Modal>
    </div>
</template>
<script>
    import axios from "axios";
    export default {
         data () {
            return {
                workOrderModal: false,
                workOrderModalLoading: true,
                type: "0",
                currentType: 0,
                currentType2: 0,
                selectTypeId: 0,
                business: "",
                typeList: [

                ],
                typeSubList: [

                ],
                businessList: [

                ],
                workOrderContent: "",
                columns: [
                    {
                        title: '',
                        key: 'work_order_number'
                    },
                    {
                        title: '',
                        key: 'machine_number'
                    },
                    {
                        title: '',
                        key: 'workstatus'
                    },
                    {
                        title: '',
                        key: 'worktype'
                    },
                    {
                        title: '',
                        key: 'department'
                    },
                    {
                       title: '',
                        key: 'business_type' 
                    },
                    {
                        title: '',
                        key: 'action',
                        width: 170,
                        align: 'center',
                        render: (h, params) => {
                            let operat = [
                                h('Button', {
                                props: {
                                    type: 'primary',
                                    size: 'small'
                                },
                                style: {
                                    marginRight: '5px'
                                },
                                on: {
                                    click: () => {
                                        this.show(params.row);
                                    }
                                }
                                }, '')
                            ];
                            return h('div',operat);
                        }
                    }
                ],
                data: [
                ],
                page: 0,
                pageTotal: 11,
                allData: []
            }
        },
        methods: {
            ShowWorkOrderModal() {
                this.workOrderModalLoading = true;
                this.workOrderModal = true;
            },
            changeSelectTypeId(id) {
                this.selectTypeId = id;
            },
            getSubType(value) {
                this.changeSelectTypeId(value);
                this.getType({
                    parent_id: value
                },"typeSubList");
            },
             ok () {
                 if(this.typeSubList.length!=0&&!this.currentType2) {
                     this.$Message.info('');
                     this.workOrderModalLoading = false;
                     return ;
                 }
                 axios.post("http://"+location.hostname+"/home/customer/insert_work_order",{
                     business_num: this.business,
                     work_order_type: this.selectTypeId,
                     work_order_content: this.workOrderContent
                 }).then(res => {
                     if(res.data.code==1) {
                         this.getData({
                            work_order_status: this.type
                        });
                         this.$Message.info('');
                         this.workOrderModal = false;
                     } else {
                         this.$Message.info(res.data.msg);
                         this.workOrderModalLoading = false;
                     }
                 });
            },
            cancel () {
                // this.$Message.info('Clicked cancel');
            },
            changeType(type) {
                if(type=="all") {
                    this.getData();
                }else {
                    this.getData({
                        work_order_status: type
                    });
                }
            },
            toPage(page) {
                // console.log(page);
                let start = (page - 1) * this.pageTotal;
                let end = (page - 1) * this.pageTotal + this.pageTotal;
                this.data = this.allData.sort((a,b) => b.id - a.id).slice(start,end);
            },
            show (data) {
                this.$Modal.info({
                    title: '',
                    content: `
                        : ${data.work_order_number}<br />
                        : ${data.business_num}<br />
                        : ${data.work_order_content}<br />
                        : ${data.workstatus}<br />
                        : ${data.complete_time}<br />
                        : ${data.worktype}<br />
                        : ${data.department}<br />
                        : ${data.business_type}<br />
                        : ${data.machine_number}<br />
                    `
                })
            },
            getData(param = {}) {
                    this.data = [];
                    this.allData = [];
                    axios.get("http://"+location.hostname+"/home/customer/show_work_order",{
                        params: param
                    }).then(res => {
                        if(res.data.code==1) {
                            this.allData = res.data.data;
                            this.data = this.allData.sort((a,b) => b.id - a.id).slice(this.page,this.pageTotal);
                        } else {
                            console.error(res.data.msg);
                        }
                    });
            },
            getType(param = {},dataAttr="typeList") {
                axios.get("http://"+location.hostname+"/home/customer/work_types",{
                    params: param
                }).then(data => {
                    if(data.data.code==1) {
                        this[dataAttr] = data.data.data;
                    } else {
                        console.error(data.data.msg);
                    }
                });
            },
            getBusinessList(param = {}) {
                axios.get("http://"+location.hostname+"/home/customer/businessList",{
                    params: param
                }).then(data => {
                    if(data.data.code==1) {
                        this.businessList = data.data.data;
                    } else {
                        console.error(data.data.msg);
                    }
                });
            }
        },
        mounted() {
            this.getData({
                work_order_status: 0
            });
            this.getType();
            this.getBusinessList();
        }
    }
</script>